import React, { FC, ReactElement, useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Auth,
  FC_GetNonReportedPlacementReport,
  FC_GetNonReportedPlacement_Interface,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import InputSelect from "../../components/Inputs/InputSelect";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { MONTHS_LIST } from "../../utils/functions";

type PlacementPlacementSummaryREBProps = {
  auth: Auth;
};

const _PlacementPlacementSummaryREB: FC<PlacementPlacementSummaryREBProps> = ({
  auth,
}): ReactElement => {
  const [loading, setLoading] = useState<boolean>(false);
  const [month, setMonth] = useState<string>(
    MONTHS_LIST[new Date().getMonth() - 1]
  );
  const [error, setError] = useState<string>("");
  const [data, setData] = useState<FC_GetNonReportedPlacement_Interface>({
    district_total_summary: [],
    pla_district_total_summary: [],
  });

  useEffect(() => {
    if (loading === true) {
      FC_GetNonReportedPlacementReport(month, "1", (status, data, err) => {
        if (data !== null) setData(data);
        if (error !== "") setError(err);
        setLoading(false);
      });
    }
  }, [loading, month, error]);

  /* function body */
  return (
    <DashboardContainer>
      <div className="py-1 border-b flex items-center">
        <div className="flex-1">
          <Heading>Non Reported placement report</Heading>
          <p className="text-gray-500">
            List of districts, with the reported and non reported {"("}Placement
            report{")"}
          </p>
        </div>
        <div className="">
          <InputSelect
            onChange={(el) => {
              setMonth(el.target.value);
              if (el.target.value !== month) setLoading(true);
            }}
            value={month}
            error={""}
            label={"Select month"}
            placeholder={"Select month"}
            name={""}
            data={MONTHS_LIST.map((el) => ({
              title: el,
              value: el,
            }))}
          />
        </div>
      </div>

      {error !== "" && <Alert theme="danger">{error}</Alert>}

      <table className="w-full">
        <thead>
          <tr className="text-left">
            <th className="p-2 border">#</th>
            <th className="p-2 border">District name</th>
            <th className="p-2 border">Total schools</th>
            <th className="p-2 border">Reported schools</th>
            <th className="p-2 border">Non Reported schools</th>
          </tr>
        </thead>
        <tbody>
          {loading === true && (
            <tr>
              <td colSpan={8} className="py-8">
                <Loading />
              </td>
            </tr>
          )}

          {data.district_total_summary.length === 0 && (
            <tr>
              <td colSpan={8} className="py-8 text-center">
                <h2 className="text-2xl">No placement found!</h2>
                <p>No placement found!</p>
              </td>
            </tr>
          )}
          {data.district_total_summary.map((itm, i) => {
            let foundPlacement = data.pla_district_total_summary.find(
              (el) => el.district_code === itm.district_code
            );
            return (
              <tr>
                <td className="border p-2">{i + 1}</td>
                <td className="border p-2">{itm.district_name}</td>
                <td className="border p-2">{itm.total}</td>
                <td className="border p-2">
                  {foundPlacement ? foundPlacement.total : 0}
                </td>
                <td className="border p-2 font-bold">
                  {foundPlacement
                    ? +itm.total - +foundPlacement.total
                    : itm.total}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </DashboardContainer>
  );
};

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const PlacementPlacementSummaryREB = connect(
  mapStateToProps,
  {}
)(_PlacementPlacementSummaryREB);
